import React from 'react';
import {
    Slider
} from 'antd';
import EditingType from '@/constant/EditingType';
import AttributeItem from './AttributeItem';

export default class LinkStyle extends React.Component {
    onSizeChange = (value) => {
        this.props.updateLinkWidth(value);
        this.props.updateEditingType(EditingType.UPDATE_STYLE);
    }

    onOpacityChange = (value) => {
        this.props.updateLinkOpacity(value);
        this.props.updateEditingType(EditingType.UPDATE_STYLE);
    }

    render() {
        const { intl, initDone, linkWidth, linkOpacity } = this.props;
        return (
            <div>
                <AttributeItem name={initDone && intl.get("Width")}>
                    <Slider max={10} step={0.5} value={linkWidth} style={{margin: '5px 0'}} onChange={this.onSizeChange}/>
                </AttributeItem>
                <AttributeItem name={initDone && intl.get("Opacity")}>
                    <Slider max={1} step={0.01} value={linkOpacity} style={{margin: '5px 0'}} onChange={this.onOpacityChange}/>
                </AttributeItem>
            </div>
        );
    }
}